@media (min-width: 1025px) {
	.page {
		max-width: 1170px;
		margin: 0 auto;
		background: #f2f3f9;
		position: relative;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-direction: column;
		flex-direction: column;
		-ms-flex-pack: center;
		justify-content: center;
		min-height: 100vh;
		box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 0px 0px, rgba(0, 0, 0, 0.3) 0px 5px 25px 0px;
	}
	body {
		background: #f2f3f9;
	}
	.horizontalMenu>.horizontalMenu-list>li>a {
		padding: 15px 18px !important;
	}
	.sticky-pin .horizontal-main {
		box-shadow: 0 2px 8px 0 rgba(40, 37, 89, 0.2);
		background: #fff;
		max-width: 1170px;
	}
	.app-header.header-search-icon {
		max-width: 1170px;
		margin: 0 auto;
	}
	.vh {
		height: 100vh;
	}
	.app-sidebar {
		left: auto !important;
	}
	.side-header {
		left: auto !important;
		right: auto !important;
		z-index: 1044;
		background: #fff;
	}
	
    .dark-mode .page {
	background: #30304d;
	
	}
	
  .horizontalMenu > .horizontalMenu-list > li > ul.sub-menu > li > ul.sub-menu{
       right: 100% !important;
	   left:auto;
}

}
